import React from "react";
import "./MsMaster.scss";
import ImageTextSection from "../../../components/ImageTextSection/ImageTextSection";

function MsMaster() {
  return (
    <div className="ms-master">
      <ImageTextSection
        title="Master of Science in Finance"
        subtitle="MSF 金融碩士"
        imageUrl="https://imgur.com/rycQiyw.png"
        imageAlt="MSF 金融碩士"
      />
      <div className="ms-master-intro">
        <p>
          金融碩士（MSF）是一個高強度、加速型的課程，專為應屆大學畢業生與職場專業人士設計，旨在培養學生的分析能力與電腦技術，特別是在以下金融領域：
        </p>
        <ul>
          <li>財務分析（Financial Analysis）</li>
          <li>估值（Valuation）</li>
          <li>財務建模（Financial Modeling）</li>
          <li>財富管理（Wealth Management）</li>
          <li>實務金融數據分析（Real World Financial Data Analytics）</li>
          <li>保險與風險管理（Insurance and Risk Management）</li>
          <li>公司治理與重組（Corporate Governance and Restructuring）</li>
          <li>資產管理（Asset Management）</li>
          <li>投資研究（Investment Research）</li>
          <li>
            金融中的深度學習與神經網絡（Deep Learning and Neural Networks in
            Finance）
          </li>
          <li>銷售與交易（Sales and Trading）</li>
        </ul>
        <p>
          這個金融碩士課程提供以量化分析為核心的全方位金融訓練，幫助學生達成個人職業目標，無論是進入投資銀行、資產管理、金融科技（FinTech）還是企業財務領域。
        </p>
      </div>
      <div className="ms-master-features">
        <h3>特色</h3>
        <ul>
          <li>短時間內強化數據與財務技能</li>
          <li>專業課程涵蓋量化金融、機器學習、深度學習應用</li>
          <li>適合希望提升技術能力的職場人士或轉職者</li>
        </ul>
      </div>
      <div className="ms-master-date">
        <h3>入學學期</h3>
        <ul>
          <li>秋季班（8月）</li>
          <li>春季班（1月初）</li>
        </ul>
      </div>
      <div className="ms-master-fiance">
        <h4>Be on the cutting edge of finance.</h4>
        <div className="ms-master-fiance-intro">
          <p>
            Broad
            商學院的金融系在國際間享有盛譽，這一聲譽來自於世界級的金融師資。他們不僅是優秀的教育者，更是頂尖的研究學者。
          </p>
          <p>
            我們的金融碩士課程（Master’s in
            Finance）是一個高強度、加速型的學習計畫，讓學生能夠在短時間內完成學位。課程規模較小，因此學生能夠獲得更多的個人化指導，並與教授建立密切的學術互動。
          </p>
        </div>

        <h3>專業培養，成就金融領域精英</h3>
        <p>
          本課程專注於培養學生成為成功的金融專業人士，重點領域包括：財務分析與投資（Financial
          Analysis and Investments）、企業治理與重組（Corporate Governance and
          Restructuring）、資產估值（Asset Valuation）、證券交易（Securities
          Trading）、保險與風險管理（Insurance and Risk
          Management），故畢業生將掌握可立即應用於金融相關職位的知識與策略。
        </p>
        <h3>靈活且前沿的課程設計</h3>
        <p>
          Broad
          的金融碩士課程提供靈活的學習模式，學生可以根據個人興趣、職業需求以及市場趨勢來選擇適合的課程。
        </p>
        <p>課程涵蓋以下核心領域：</p>
        <ul>
          <li>財務管理（Financial Management）</li>
          <li>企業財務（Corporate Finance）</li>
          <li>投資（Investments）</li>
          <li>定量風險管理（Quantitative Risk Management）</li>
          <li>國際金融（International Finance）</li>
          <li>金融機構（Financial Institutions）</li>
          <li>
            金融中的深度學習與神經網絡（Deep Learning and Neural Networks in
            Finance）
          </li>
          <li>金融數據分析（Financial Data Analytics）</li>
        </ul>
      </div>
      <div className="ms-master-require">
        <h2>課程要求 | Degree Requirements</h2>
        <div className="ms-master-require-intro">
          <h3>學位要求總學分：30 學分</h3>
          <p>
            課程計畫需獲得課程主任（Program
            Director）批准，所有學生需滿足以下要求：
          </p>
          <ul>
            <li>維持最低 GPA 3.25</li>
            <li>
              修畢至少18學分的金融系（Department of Finance）800 級或以上課程：
            </li>
            <span>
              這 18 學分必須包含 FI 801（Managerial Finance，管理財務）或 FI
              980（Theory of Finance，財務理論），除非學生通過系所的免修考試。
            </span>
            <li>修畢12學分的選修課程（須經課程主任批准）：</li>
            <span>
              可選金融相關課程，或經濟學（Economics）、統計學（Statistics）、數學（Mathematics）等相關學科的課程。其中至少9學分
              必須來自800級或更高等級的課程。
            </span>
          </ul>
        </div>
        <div className="ms-master-require-select">
          <h3>靈活的課程選擇 | Flexible Course Selection</h3>
          <p>
            金融碩士課程的最大特色是課程選擇的靈活性。在符合學院大方向規範的前提下，學生可根據自己的職業目標和學術背景規劃學習內容，確保課程與未來職涯發展相契合。
          </p>
          <div className="ms-master-require-select-list">
            <h3>M.S. in Finance 課程涵蓋眾多重要金融領域，包括：</h3>
            <ul>
              <li>金融建模（Financial Modeling）</li>
              <li>投資（Investments）</li>
              <li>風險管理（Risk Management）</li>
              <li>實務金融數據分析（Real-World Financial Data Analytics）</li>
              <li>
                金融中的深度學習與神經網絡（Deep Learning and Neural Networks in
                Finance）
              </li>
              <li>國際金融（International Finance）</li>
            </ul>
          </div>
        </div>
        <div className="ms-master-require-plan">
          <h3>範例課程安排（30 學分） | Example Course Plan (30 Credits)</h3>
          <div className="ms-master-require-plan-list">
            <h3>秋季學期 | Fall Semester</h3>
            <ul>
              <li>FI 801: Managerial Finance（管理財務） 3學分</li>
              <li>
                FI 845/846: Financial Modeling and Simulation（金融建模與模擬）
                3學分
              </li>
              <li>
                FI 853/854: Debt, Money Instruments and
                Markets（債務、貨幣工具與市場） 3學分
              </li>
              <li>
                FI 860/861: Multinational Corporate Finance（跨國企業財務）
                3學分
              </li>
            </ul>
            <h3>春季學期 | Spring Semester</h3>
            <ul>
              <li>
                FI 850/851: Introduction to Investments（投資學概論） 3學分
              </li>
              <li>FI 852/855: Financial Derivatives（金融衍生工具） 3學分</li>
              <li>
                FI 862/865: Corporate Strategy Valuation（企業戰略估值） 3學分
              </li>
              <li>
                FI 872/873: Financial Data Analytics（金融數據分析） 3學分
              </li>
            </ul>
            <h3>夏季學期 | Summer Semester</h3>
            <ul>
              <li>FI 414: Advanced Business Finance（高級企業財務） 3學分</li>
              <li>FI 890: International Business Law（國際商業法） 3學分</li>
            </ul>
            <h3>其他可選課程 | Examples of Available Courses</h3>
            <ul>
              <li>
                FI 891: Deep Learning and Neural Networks in
                Finance（金融中的深度學習與神經網絡） 3 學分
              </li>
              <li>
                FI 891: Quantitative Risk Management（量化風險管理） 3 學分
              </li>
              <li>
                FI 821: Cross Section Econometrics（橫斷面計量經濟學） 3 學分
              </li>
              <li>
                FI 822: Time Series Econometrics（時間序列計量經濟學） 3 學分
              </li>
              <li>
                FI 857: Security Analysis and Portfolio
                Management（證券分析與投資組合管理） 3 學分
              </li>
            </ul>
          </div>
        </div>
        <div className="ms-master-require-apply">
          <h3>學習成果</h3>
          <p>完成課程後，你將具備以下能力：</p>
          <div className="ms-master-require-apply-features">
            <div className="feature-block">
              <div className="feature-title">整合行銷策略</div>
              <div className="feature-desc">
                識別並把握市場機會，設計有效的行銷解決方案。
              </div>
            </div>
            <div className="feature-block">
              <div className="feature-title">市場調查與分析</div>
              <div className="feature-desc">
                設計並進行市場調查，將數據轉化為管理決策的依據。
              </div>
            </div>
            <div className="feature-block">
              <div className="feature-title">廣告與宣傳規劃</div>
              <div className="feature-desc">
                制定跨媒體的廣告與促銷計劃，包括線上、電視、廣播和印刷媒體。
              </div>
            </div>
            <div className="feature-block">
              <div className="feature-title">消費者行為分析</div>
              <div className="feature-desc">
                理解顧客如何評估產品並做出購買決策，制定相應的行銷策略。
              </div>
            </div>
            <div className="feature-block">
              <div className="feature-title">國際市場拓展</div>
              <div className="feature-desc">
                將行銷策略擴展至國際市場，拓展產業版圖。
              </div>
            </div>
          </div>
        </div>
        <div className="ms-master-require-career">
          <h3>職涯發展方向</h3>
          <p>獲得市場營銷方向的MBA學位後，你將有機會從事以下職位：</p>
          <div className="ms-master-require-career-features">
            <div className="feature-block">
              <div className="feature-title">行銷經理</div>
              <div className="feature-subtitle">Marketing Manager</div>
              <div className="feature-desc">負責制定和執行企業整體行銷戰略</div>
            </div>
            <div className="feature-block">
              <div className="feature-title">品牌策略師</div>
              <div className="feature-subtitle">Brand Strategist</div>
              <div className="feature-desc">規劃品牌定位並提升品牌價值</div>
            </div>
            <div className="feature-block">
              <div className="feature-title">數位行銷專家</div>
              <div className="feature-subtitle">
                Digital Marketing Specialist
              </div>
              <div className="feature-desc">
                透過數位平台推動品牌曝光與客戶參與。
              </div>
            </div>
            <div className="feature-block">
              <div className="feature-title">市場研究分析師</div>
              <div className="feature-subtitle">Market Research Analyst</div>
              <div className="feature-desc">
                研究市場趨勢並提供數據支持的決策建議
              </div>
            </div>
            <div className="feature-block">
              <div className="feature-title">產品經理</div>
              <div className="feature-subtitle">Product Manager</div>
              <div className="feature-desc">
                負責產品開發、上市策略及市場表現評估。
              </div>
            </div>
          </div>
        </div>

        <div className="ms-master-cfa">
          <h2>準備 CFA 認證 | Prepare for CFA Certification</h2>
          <div className="ms-master-cfa-intro">
            <h3>MSF 課程助力 CFA 考試成功</h3>
            <p>
              本課程是官方 CFA 計畫合作夥伴（CFA Program Partner），課程內容涵蓋
              70% 以上的 CFA Body of Knowledge，幫助學生掌握 CFA
              考試所需的關鍵知識與工具。
            </p>
            <div className="ms-master-cfa-recognition">
              <h3>關於 CFA 認證</h3>
              <p>
                特許金融分析師 (CFA, Chartered Financial Analyst) 被認為是 CFA
                協會 (CFA Institute)
                頒發的專業資格證書，廣受全球投資業界認可，CFA
                認證對於金融專業人士的職業發展有著重要價值。
              </p>
              <p className="cfa-note">
                * CFA Program Partner:
                https://www.cfainstitute.org/en/university-partners/university-affiliation#sort=%40cfainstitutemembercount%20descending
              </p>
            </div>
          </div>
        </div>

        <div className="ms-master-network">
          <h2>建立專業人脈 | Build a Professional Network</h2>
          <div className="ms-master-network-intro">
            <h3>強調團隊合作，打造紮實社群</h3>
            <p>
              透過金融產業專業講座，以及MSF課程中學生之間高度合作，彼此支持，共同學習。這種緊密的專業網絡將幫助你在未來的職業發展中取得更多機會。
            </p>
            <h3>建立職涯人脈，收穫終身友誼</h3>
            <p>
              在MSU就讀MSF課程的過程中，您將有機會與來自全世界的同學建立關係，這不僅僅是短暫的課堂關係，也將成為您一生的寶貴資產。
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default MsMaster;
